/* App.css */

body {
  font-family: Arial, sans-serif;
  background-color: white;
  margin: 0;
  padding: 0;
}

.app-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: white;
  margin-top: 50px;
}

.app-header {
  text-align: center;
  margin-bottom: 20px;
}

.twitter-logo {
  width: 80px;
  margin-bottom: 10px;
}

h1 {
  color: #1da1f2;
  font-size: 24px;
}

h3 {
  color: #1da1f2;
  margin-bottom: 10px;
}

.input-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 30px;
}

.tweet-container {
  margin-bottom: 20px;
}

.input-field, .input-textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.input-textarea {
  height: 100px;
  resize: none;
}

.action-button {
  background-color: #1da1f2;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
  font-size: 16px;
}

.action-button:hover {
  background-color: #0d8cdc;
}

.result-container {
  margin-top: 30px;
  background-color: #f1f1f1;
  padding: 15px;
  border-radius: 8px;
  border: 1px solid #e1e8ed;
  max-height: 300px;
  overflow-y: auto;
}

pre {
  font-size: 14px;
  color: #333;
  white-space: pre-wrap;
  word-wrap: break-word;
}
